@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
.wrap {
  display: flex;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 32px;
}

@media (max-width: 1199px) {
  .wrap {
    padding: 0 16px;
  }
}

.about {
  margin-bottom: 44px;
}

.text {
  color: rgb(116, 168, 63);
}

.text-net {
  color: rgb(80, 82, 78);
}

.text-cv {
  cursor: pointer;
  padding: 10px;
  width: 11em;
  border-bottom: 4px solid red;
  width: 12em;
}

.menu {
  margin-top: 10px;
}

.title {
  width: 21em;
  border-bottom: 4px solid black;
}

.description {
  line-height: 2.2;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media (max-width: 768px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}

.container-item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.container-item > h4 {
  margin-bottom: 0;
}
*, *::before, *::after {
    box-sizing: border-box;
}

html, body #root {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 2.2;
    font-family: 'Roboto Mono', monospace, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a:visited {
    color: black;
}

a {
    cursor: pointer;
    text-decoration: none;
}
